import Axios from 'nextcloud-axios';
import { mapState } from 'vuex';
import { Multiselect } from 'nextcloud-vue';
import _ from 'lodash';
export default {
  name: "AdminTwoFactor",
  components: {
    Multiselect: Multiselect
  },
  beforeMount: function beforeMount() {
    this.$store.dispatch('getAllApps');
    this.$store.dispatch('getEnabledProvidersCurrentUser');
  },
  data: function data() {
    return {
      loading: false,
      dirty: false,
      groups: [],
      loadingGroups: false
    };
  },
  computed: {
    enforced: {
      get: function get() {
        return this.$store.state.security.enforced;
      },
      set: function set(val) {
        this.dirty = true;
        this.$store.commit('setEnforced', val);
      }
    },
    enforcedGroups: {
      get: function get() {
        return this.$store.state.security.enforcedGroups;
      },
      set: function set(val) {
        this.dirty = true;
        this.$store.commit('setEnforcedGroups', val);
      }
    },
    excludedGroups: {
      get: function get() {
        return this.$store.state.security.excludedGroups;
      },
      set: function set(val) {
        this.dirty = true;
        this.$store.commit('setExcludedGroups', val);
      }
    },
    noProviderGlobally: {
      get: function get() {
        var providers = this.$store.getters.getAllApps.filter(function (app) {
          return 'two-factor-providers' in app && 'provider' in app['two-factor-providers'] && app['active'] === true;
        });
        return providers.length === 0;
      }
    },
    noProviderAdmin: {
      get: function get() {
        var providers = this.$store.getters.getEnabledProvidersCurrentUser;
        return providers.length === 0;
      }
    }
  },
  mounted: function mounted() {
    // Groups are loaded dynamically, but the assigned ones *should*
    // be valid groups, so let's add them as initial state
    console.log(this.enforcedGroups);
    this.groups = _.sortedUniq(_.uniq(this.enforcedGroups.concat(this.excludedGroups))); // Populate the groups with a first set so the dropdown is not empty
    // when opening the page the first time

    this.searchGroup('');
  },
  methods: {
    searchGroup: _.debounce(function (query) {
      var _this = this;

      this.loadingGroups = true;
      Axios.get(OC.linkToOCS("cloud/groups?offset=0&search=".concat(encodeURIComponent(query), "&limit=20"), 2)).then(function (res) {
        return res.data.ocs;
      }).then(function (ocs) {
        return ocs.data.groups;
      }).then(function (groups) {
        return _this.groups = _.sortedUniq(_.uniq(_this.groups.concat(groups)));
      }).catch(function (err) {
        return console.error('could not search groups', err);
      }).then(function () {
        return _this.loadingGroups = false;
      });
    }, 500),
    saveChanges: function saveChanges() {
      var _this2 = this;

      this.loading = true;
      var data = {
        enforced: this.enforced,
        enforcedGroups: this.enforcedGroups,
        excludedGroups: this.excludedGroups
      };
      Axios.put(OC.generateUrl('/settings/api/admin/twofactorauth'), data).then(function (resp) {
        return resp.data;
      }).then(function (state) {
        _this2.state = state;
        _this2.dirty = false;
      }).catch(function (err) {
        console.error('could not save changes', err);
      }).then(function () {
        return _this2.loading = false;
      });
    }
  }
};